import { useEffect, useState } from "react";
import { VideoPlayer } from "../..";
import { Refresh, Server } from "../../assets";
import "./video.scss";

const ButtonOption = (Symbol: any, text: string) => {
    return (
        <div className="button__with-icon">
            {<Symbol />}
            <div> {text}</div>
        </div>
    );
};

const Video = () => {
    const [toggleServer, setToggleServer] = useState(false);

    // useful links
    // https://sports.genzaitv.com/2022/11/fifa-world-cup-2022-live-broadcasting.html.html

    const [url, setUrl] = useState(
        // "https://leaver.bongstreams.com/live/adaptive_bong-1/live/bong-1/chunks.m3u8"
        "https://s2.wecast.to:8443/hls/6XtqfQdxa4wI/index.m3u8?st=IPIwMOCX5grFn1hRxt7o9w&e=1669390661"
    );
    const [activeServer, setActiveServer] = useState("FS1 (eng)");

    // eslint-disable-next-line
    const [serverList, _] = useState([
        {
            id: 2,
            Name: "TSN (eng)",
            url: "https://s2.wecast.to:8443/hls/6XtqfQdxa4wI/index.m3u8?st=IPIwMOCX5grFn1hRxt7o9w&e=1669390661"
        },
        // {
        //     id: 3,
        //     Name: "FS1 (eng)",
        //     url: "https://wswanata.azureedge.net/live/FS1/chunks.m3u8",
        // },
        // {
        //     id: 2,
        //     Name: "TSN1 (eng, 720p)",
        //     url: "https://griomreavigremant654.s3.amazonaws.com/redzone_720p60.m3u8",
        // },
        {
            id: 1,
            Name: "TSN1 (eng, 1)",
            url: "https://rtv-murcia-live.globalmest.com/stream/abr/k1.m3u8",
        },
    ]);

    /*  */
    /*   Video Refresh   */
    const [seed, setSeed] = useState(1);
    const refreshVideo = () => {
        setSeed(Math.random());
    };
    /*  end  Refresh  */
    /*  */

    const Servers = (activeServer: string, ServerName: string, url: string) => {
        let divClassName = "";
        if (activeServer === ServerName) {
            divClassName = "active";
        }
        return (
            <div
                className={divClassName}
                onClick={() => {
                    setActiveServer(ServerName);
                    setUrl(url);
                }}
            >
                {ServerName}
            </div>
        );
    };

    const handleSSelect = () => {
        setToggleServer(!toggleServer);
    };

    useEffect(() => {
        refreshVideo();
    }, [url]);

    return (
        <div id="VIDEO">
            <div className="VIDEO__player">
                <VideoPlayer url={url} key={seed} />
            </div>

            <div className="options">
                <div className="refresh" onClick={refreshVideo}>
                    {ButtonOption(Refresh, "Refresh")}
                </div>
                <div className="option__server">
                    <div className="server__select" onClick={handleSSelect}>
                        {ButtonOption(Server, "Switch Channel")}
                    </div>
                    <div className={toggleServer ? "servers" : "hide"}>
                        {serverList.map(
                            (server: {
                                Name: string;
                                url: string;
                                id: any;
                            }): any => (
                                <div key={server.id}>
                                    {Servers(
                                        activeServer,
                                        server.Name,
                                        server.url
                                    )}
                                </div>
                            )
                        )}
                    </div>
                </div>
            </div>
        </div>
    );
};
export default Video;
